
@media screen and (max-width: 992px) {
html, body {
    background-color: #e2e4e6;
    font-size: 3vw
}
.form-control {
    height: 100%;
    font-size: 4vw
}
#label {
    font-size: 3vw
}
#item-box {
    border-radius: 15px
}
#github {
    height: 5vw;
}
div #wraper {
  height: 95%;
}
div #t {
    height: 95%;
}
#update img {
    height: 3vw;
    -ms-flex-align: center;
        align-items: center
}
}
@media screen and (min-width: 992px) {
html, body {
    background-color: #7D7DA5;
}
#item-box {
    border-radius: 5px
}
#github {
    height: 3vw;
}
div #wraper {
  height: 90%;
}
div #t {
    height: 90%;
}
#update img {
    height: 18px;
    -ms-flex-align: center;
        align-items: center
}
}
html, body {
    height: 100%;
    overflow-y: hidden;
    color: #000;
    font-family: 微软雅黑细体;
}
#app {
  height: 100%
}
#github img {
  height: 100%;
}
#github {
  margin-top: 20px;
}





